"use client";

import { useTheme } from "./ThemeProvider";

export function ThemeToggle() {
  const { theme, toggle } = useTheme();

  return (
    <button
      type="button"
      onClick={toggle}
      aria-label="Toggle light and dark mode"
      className="inline-flex h-9 w-9 items-center justify-center rounded-full border border-border-light bg-surface-light text-xs text-text-mutedLight shadow-sm transition hover:bg-slate-100 dark:border-border-dark dark:bg-surface-dark dark:text-text-mutedDark dark:hover:bg-slate-800"
    >
      {theme === "light" ? <span aria-hidden="true">☾</span> : <span aria-hidden="true">☼</span>}
    </button>
  );
}


